<template>
  <div>
    <ButtonsChild :buttons="buttons" :cols="cols"/>
    <v-slider
      class="mx-4 mt-1"
      v-model="speed"
      thumb-label
      label="speed"
      hide-details
    ></v-slider>
  </div>
</template>

<script>
import ButtonsChild from '@/components/controll/child/ButtonsChild.vue'
import { mapActions } from 'vuex'

export default ({
  name: 'MoveControlMod',
  components: {
    ButtonsChild
  },
  data: () => ({
    buttons: [
      [false, 'X +', 'X_add', 87, 'XS'],
      '',
      [false, 'X -', 'X_minus', 87, 'XS'],
      [false, 'Y +', 'Y_add', 87, 'YS'],
      '',
      [false, 'Y -', 'Y_minus', 87, 'YS'],
      [false, 'Z +', 'Z_add', 87, 'ZS'],
      '',
      [false, 'Z -', 'Z_minus', 87, 'ZS'],
      [false, 'G +', 'G_add', 87, 'GS'],
      '',
      [false, 'G -', 'G_minus', 87, 'GS']
    ],
    cols: 3,
    speed: 100,
    timmer: null
  }),
  methods: {
    ...mapActions(['changeWsContent'])
  },
  watch: {
    speed () {
      if (this.timmer) {
        clearTimeout(this.timmer)
      }
      this.timmer = setTimeout(() => {
        this.changeWsContent('wsB ' + String(this.speed))
      }, 300)
    }
  }
})
</script>

